            isShow: false,
            activeBtn:0,
            tabs:['APP下载','SDK下载','推荐浏览器下载']
        }
    },
    created() {
        this.bus.$emit('setHeader', '');
    },
    methods: {
        toggleTabs: function(index) {
            this.nowIndex = index;
        },
        goAnchor(selector, index) {
          // this.activeBtn = index;
          var _this = this;
          var nub=0;
          var timer = setInterval(function () {
            nub+=10;
                if (_this.$el.querySelector(selector).offsetTop-80 <= nub) {
                    clearInterval(timer)
                }
                window.scrollTo(0, nub)
            }, 1)
        }
    },
    mounted() {
        var options={
            live:false
        };
        new WOW(options).init();
    }
}

