<template>
    <div class="header">
        <div v-bind:class="['public-header',{ active: isActive },Assit]">
            <div class="body-box clearfix">
                <router-link to="/">
                    <div class="logo-box"></div>
                </router-link>
                <div class="nav-box">
                    <ul>
                        <li>
                            <router-link to="/">
                                首页
                            </router-link>
                        </li>
                        <li>
                            <router-link to="pro">
                                产品方案
                            </router-link>
                        </li>
                        <li>
                            <router-link to="serve">
                                增值服务
                            </router-link>
                        </li>
                        <li>
                            <router-link to="down">
                                下载中心
                            </router-link>
                        </li>
                        <li>
                            <router-link to="assit">
                                帮助中心
                            </router-link>
                        </li>
                        <li>
                            <router-link to="about">
                                关于我们
                            </router-link>
                        </li>
                    </ul>
                </div>
                <div class="tel-box">
                    <i class="icon"></i>4008-922-902
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Header',
    data() {
        return {
            Assit:'',
            isActive: false
            
        }
    },
    created() {
        var _this = this;
        this.bus.$on('setHeader',function(msg){
            _this.Assit = msg;
        })
    },
    methods: {
        handleScroll: function() {
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            if (scrollTop > 100) {
                this.isActive = true;
            } else {
                this.isActive = false;
            }
        }
    },
    mounted() {
        window.addEventListener('scroll', this.handleScroll)
    }
}
</script>
<style scoped lang="less">
    .public-header {
        position: fixed;
        left: 0;
        width: 100%;
        top: 0;
        z-index: 999999;
        box-sizing: border-box;
        padding-top: 30px;
        padding-bottom: 30px;
        padding-left: 75px;
        transition: all .3s;
        .body-box {
            overflow: hidden;
            line-height: 45px;
            height: 45px;
            .logo-box {
                float: left;
                width: 158px;
                height: 45px;
                margin-right: 80px;
                background: url(./../assets/images/logo.png) center left no-repeat;
            }
            li {
                position: relative;
                float: left;
                text-align: center;
                margin-right: 50px;
                a {
                    position: relative;
                    display: inline-block;
                    color: #fff;
                    font-size: 16px;
                    font-weight: 400;
                    vertical-align: middle;
                    overflow: hidden;
                    width: 100%;
                    box-sizing: border-box;
                }
                &:hover {
                    a {
                        transition: color .3s;
                        opacity: .7;
                    }
                }
            }
        }
        .tel-box{
            float:left;
            color: #fff;
            font-size: 16px;
            .icon{
                position: relative;
                margin-right: 8px;
                top:-3px;
                display: inline-block;
                width:19px;
                height: 19px;
                background: url(./../assets/images/icon_6.png);
                background-position: 19px 0px ;
                vertical-align:middle;
            }
        }
        &.active {
            background: #fff;
            transition: all .3s;
            padding-top: 20px;
            padding-bottom: 20px;
            box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.1);
            .logo-box {
                background: url(./../assets/images/logo_1.png) center left no-repeat;
                background-size: auto 40px;
                width: 139px;
                height: 40px;
            }
            .body-box{
                line-height: 40px;
                height: 40px;
            }
            li {
                a {
                    color: #333;
                    
                }
            }
            .tel-box{
                color: #333;
                .icon{
                    background-position: 0px 0px ;
                }
            }
        }
        &.assit{
            background: #fff;
            transition: all 0s;
            padding-top: 20px;
            padding-bottom: 20px;
            box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.1);
            .logo-box {
                background: url(./../assets/images/logo_1.png) center left no-repeat;
                background-size: auto 40px;
                width: 139px;
                height: 40px;
            }
            .body-box{
                line-height: 40px;
                height: 40px;
            }
            li {
                a {
                    color: #333;
                    
                }
            }
            .tel-box{
                color: #333;
                .icon{
                    background-position: 0px 0px ;
                }
            }
        }
    }

</style>



