<template>
    <div class="public-footer">
        <div class="block">
            <span>© 2010 - 2019 三芒世纪（北京）科技有限公司版权所有  京ICP备12048093号-1</span>
            <span>支持邮箱：help@3mang.com</span>
            <span>商务邮箱：marketing@3mang.com </span>
            <span>咨询电话：4008922902</span>
            <span class="attention">
                关注我们 <img src="../assets/images/icon_2.png">
                <div class="er-box">
                    <img src="../assets/images/icon_3.png">
                    <em class="corner"></em>
                </div>
            </span>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Footer',
    data() {
        return {
            msg: '这是底部'
        }
    }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.block{
    width: 1240px;
}
.public-footer {
    height: 100px;
    background: #333333;
    color: #fff;
    line-height: 100px;
    position: relative;
    z-index:999;
    span{
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
    }
    .attention{
        position: relative;
        float: right;
        margin-right:0;
        img{
            vertical-align:middle;
            margin-left: 5px;
        }
        .er-box{
            display: none;
            position:absolute;
            bottom: 90px;
            left: 30px;
            padding: 5px;
            background:#fff;
            box-sizing: border-box;
            img{
                width: 100px;
                margin-left: 0;
            }
            .corner{
                  border: 10px solid transparent;
                  border-top: 10px solid #fff;
                  position: absolute;
                  bottom: -20px;
                  right: 50%;
                  margin-right: -10px;
                  z-index: 10;
                  *height: 0;
            }
        }
        &:hover{
            .er-box{
                display: block;
            }
        }
    }
}

</style>



