<template>
    <div class="player">
        <video id="myVideo" class="video-js" width="100%" height="100%" poster="/static/images/img_7.jpg">
            <source src="/static/file/xuedianyun_demo.mp4" type="video/mp4">
        </video>
    </div>
</template>
<script>
export default {
    name: "Player",
    data() {
        return {};
    },
    mounted() {
        this.initVideo();
    },
    methods: {
        initVideo() {
            let myPlayer = this.$video(myVideo, {
                controls: true,
                width: "1200px",
                height: "586px"
            });
        }
    }
};

</script>
<style lang="less">
    .player{
        .video-js .vjs-big-play-button{
            width: 74px;
            height: 74px;
            background: url(/assets/images/icon_45.png);
            outline: none;
            border:none;
            font-size: 0;
            top: 50%;
            margin-top: -37px;
            left: 50%;
            margin-left: -37px;
        }
    }
    
</style>



