import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/Home'
import Pro from '@/views/Pro'
import Serve from '@/views/Serve'
import Down from '@/views/Down'
import Assist from '@/views/Assist'
import About from '@/views/About'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/pro',
      name: 'Pro',
      component: Pro
    },
    {
      path: '/serve',
      name: 'Serve',
      component: Serve
    },
    {
      path: '/down',
      name: 'Down',
      component: Down
    },
    {
      path: '/assit',
      name: 'Assist',
      component: Assist
    },
    {
      path: '/about',
      name: 'About',
      component: About
    }
  ]
})



