<template>
    <div class="info block">
        <swiper :options="swiperOption">
            <swiper-slide>
                <div class="item-box item_1">
                    <div class="img-box">
                        <img src="./../assets/images/icon_64.png">
                        <div class="icon-box">
                             <img src="./../assets/images/icon_65.png">
                        </div>
                    </div>
                    <div class="txt-box">
                        <h3>高并发、低延迟，万人直播、轻松应对</h3>
                        <p>在国内和海外部署了数百台服务器，并多线级联部署了多家运营商网络，确保全球用户高清流畅不卡顿</p>
                        <a href=""><span>免费试用</span></a>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="item-box item_2">
                    <div class="txt-box">
                        <h3>授课交流胜似面授，零距离将面授上课过程还原到直播教室</h3>
                        <a href=""><span>免费试用</span></a>
                    </div>
                    <div class="img-box img_2">
                        <img src="./../assets/images/icon_66.png">
                        <div class="icon-box">
                             <img src="./../assets/images/icon_67.png">
                        </div>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="item-box item_3">
                        <div class="img-box">
                        <img src="./../assets/images/icon_69.png">
                        <div class="icon-box">
                             <img src="./../assets/images/icon_68.png">
                        </div>
                    </div>
                    <div class="txt-box">
                        <h3>多终端自由切换 无需下载客户端</h3>
                        <p>PC/MAC/PAD/手机/微信/小程序，随时随地上课</p>
                        <a href=""><span>免费试用</span></a>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="item-box item_4">
                    <div class="txt-box">
                        <h3>在线教室可装修，一键切换教室界面，满足你个性化场景教学需求</h3>
                        <a href=""><span>免费试用</span></a>
                    </div>
                     <div class="img-box">
                        <img src="./../assets/images/icon_70.png">
                        <div class="icon-box">
                             <img src="./../assets/images/icon_71.png">
                        </div>
                    </div>
                </div>
            </swiper-slide>
             <swiper-slide>
                <div class="item-box item_5">
                    <div class="img-box">
                        <img src="./../assets/images/icon_72.png">
                        <div class="icon-box">
                             <img src="./../assets/images/icon_73.png">
                        </div>
                    </div>
                    <div class="txt-box">
                        <h3>开放API和SDK接口，1天即可与现有网站平台完成无缝对接</h3>
                        <a href=""><span>免费试用</span></a>
                    </div>
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>
<script>
import './../assets/css/swiper.min.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    name: "Info",
    data() {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet: function (index, className) {
                      return '<div class="' + className + '"><span></span><i></i></div>';
                    }
                },
                autoplay: {
                    delay: 5000,
                    disableOnInteraction: false,
                }

            }
        }
    },
    components: {
        swiper,
        swiperSlide
    },
    methods: {

    }
};

</script>
<style scoped lang="less">
.info{
    width: 1300px;
    padding-top: 100px;
    padding-bottom: 100px;
}
.item-box{
    font-size: 0;
}
.img-box {
    position: relative;
    display: inline-block;
    width: 650px;
    &.img_3{
        text-align: right;
    }
}

.txt-box {
    display: inline-block;
    vertical-align:middle;
    width: 650px;
    h3 {
        position: relative;
        font-size: 30px;
        color: #1c2a37;
        line-height: 1.6;
        padding-top: 20px;
        margin-bottom: 45px;
        font-weight: 500;
        &:before {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 46px;
            height: 5px;
            background: -webkit-gradient(linear, left top, right top, color-stop(10%, #4febc2), color-stop(50%, #4fdbea));
            background: linear-gradient(to right, #4febc2 10%, #4fdbea 50%);
        }
    }
    p{
        font-size: 18px;
        color:#808080;
        line-height: 1.6;
        margin-bottom: 55px;
    }
    a{
          display: inline-block;
          height: 50px;
          line-height: 48px;
          vertical-align: middle;
          border-top-right-radius: 30px;
          border-bottom-right-radius: 30px;
          padding-right: 20px;
          border-top: 1px solid #979797;
          border-bottom: 1px solid #979797;
          border-right: 1px solid #979797;
          font-weight: 500;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          position: relative;
          margin-left: 20px;
          span{
            position: relative;
              z-index: 3;
              left: -20px;
              -webkit-transition: all 0.3s;
              transition: all 0.3s;
              font-size: 18px;
          }
        &:hover{
            span{
                left: 0;

            }
        }
    }
}
.item_1{
    .img-box{
        .icon-box{
            position:absolute;
            top: 15px;
            left: 220px;
            animation: anime_1 2s linear infinite;
        }
    }
}
.item_2{
    .img-box{
        text-align:right;
        .icon-box{
            position:absolute;
            top: 20px;
            right: 160px;
            animation: anime_1 2s linear infinite;
        }
    }
}
.item_3{
    .img-box{
        padding-top: 20px;
        .icon-box{
            position:absolute;
            top: 10px;
            right: 260px;
            animation: anime_1 2s linear infinite;
        }
    }
}
.item_4{
    .img-box{
        text-align: right;
        padding-right: 95px;
        box-sizing: border-box;
        .icon-box{
            position:absolute;
            top: 80px;
            right: 0;
            animation: anime_1 2s linear infinite;
        }
    }
}
.item_5{
    .img-box{
        box-sizing: border-box;
        .icon-box{
            position:absolute;
            top: 15px;
            left: 15px;
            animation: anime_1 2s linear infinite;
        }
    }
}
@media screen and (max-width: 1600px) {
    .info{
        width: 1200px;
        padding-top: 80px;
        padding-bottom: 80px;
    }
    .img-box{
        width:600px;
    }
    .txt-box{
        width: 600px;
    }
}
</style>



