                                    <th>说明</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>2014-11-18</td>
                                    <td>1.0</td>
                                    <td>刘列涛</td>
                                    <td>统一整理，校正</td>
                                </tr>
                                <tr>
                                    <td>2014-12-10</td>
                                    <td>1.1</td>
                                    <td>刘列涛</td>
                                    <td>新增文档列表查询接口</td>
                                </tr>
                                <tr>
                                    <td>2014-12-21</td>
                                    <td>1.2</td>
                                    <td>刘列涛</td>
                                    <td>新增移劢端加入课堂接口</td>
                                </tr>
                                <tr>
                                    <td>2015-03-16</td>
                                    <td>1.3</td>
                                    <td>刘列涛</td>
                                    <td>新增查询单个课堂中用户进出总记录接口</td>
                                </tr>
                                <tr>
                                    <td>2015-03-16</td>
                                    <td>1.3</td>
                                    <td>刘列涛</td>
                                    <td>新增设置站点的求劣接口</td>
                                </tr>
                                <tr>
                                    <td>2015-03-25</td>
                                    <td>1.4</td>
                                    <td>刘列涛</td>
                                    <td>新增站点当前在线总人数接口</td>
                                </tr>
                                <tr>
                                    <td>2015-06-20</td>
                                    <td>1.5</td>
                                    <td>王衍喜</td>
                                    <td>新增测试站点添加、编辑接口</td>
                                </tr>
                                <tr>
                                    <td>2015-07-14</td>
                                    <td>1.6</td>
                                    <td>刘列涛</td>
                                    <td>新增查询课堂最多在线人数接口</td>
                                </tr>
                                <tr>
                                    <td>2015-09-22</td>
                                    <td>1.7</td>
                                    <td>雒亚强</td>
                                    <td>新增文档服务器接口、文档上传接口、文档删除接口</td>
                                </tr>
                                <tr>
                                    <td>2015-09-24</td>
                                    <td>1.8</td>
                                    <td>雒亚强</td>
                                    <td>淘宝加入课堂接口</td>
                                </tr>
                                <tr>
                                    <td>2015-10-15</td>
                                    <td>1.9</td>
