                                    <td>雒亚强</td>
                                    <td>H5 接口</td>
                                </tr>
                                <tr>
                                    <td>2015-11-10</td>
                                    <td>2.1</td>
                                    <td>雒亚强</td>
                                    <td>修改了获取服务器列表的接口、文档上传接口</td>
                                </tr>
                                <tr>
                                    <td>2015-12-08</td>
                                    <td>2.2</td>
                                    <td>雒亚强</td>
                                    <td>pc 接口和 h5 接口合幵</td>
                                </tr>
                                <tr>
                                    <td>2015-12-14</td>
                                    <td>2.3</td>
                                    <td>雒亚强</td>
                                    <td>接口 5.2、5.3、5.4 新增了字段</td>
                                </tr>
