                                    <td>2016-02-19</td>
                                    <td>2.4</td>
                                    <td>雒亚强</td>
                                    <td>新增 app 获取课堂参数接口</td>
                                </tr>
                                <tr>
                                    <td>2016-02-22</td>
                                    <td>2.5</td>
