                </div>
                <div class="item-box" v-show="nowIndex===2">
                    常见问题
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Assist',
    data() {
        return {
            tabsParam:['接口文档','新手使用文档','常见问题'],
            nowIndex:0,
            isShow:false
        }
    },
    created() {
        this.bus.$emit('setHeader', 'assit');
    },
    methods: {
        toggleTabs:function(index){
            this.nowIndex = index;
            window.scrollTo(0,0);
        }
    }
}
