        width: 360px;
        left: 0;
        bottom: 0;
        background:#ffffff;
        text-align: center;
        padding-top: 70px;
        h3{
            font-size: 22px;
            color: #262626;
            font-weight: 400;
            margin-bottom: 38px;
        }
        li{
            margin-bottom: 20px;
            span{
                display: inline-block;
                width:175px;
                height: 50px;
                box-sizing: border-box;
                line-height: 48px;
                font-size: 18px;
                color: #262626;
                border:1px solid #262626;
                border-radius: 30px;
                cursor: pointer;
                &:hover{
