        color: #fff;
        margin-bottom: 40px;
        &:last-child{
            margin-bottom: 0;
        }
        .title{
            font-size: 30px;
            line-height: 1;
            margin-bottom: 35px;
        }
        h3{
            font-size: 24px;
            font-weight: 400;
            line-height: 1;
            margin-bottom: 20px;

        }
        h4{
            font-weight: 400;
            line-height: 1;
            font-size: 18px;
            margin-bottom: 5px;
            &.mt20{
                margin-top: 20px;
            }
        }
        p{
            line-height: 2;
            font-size: 14px;
        }
        table{
            width: 100%;
            tr{
                td,th{
                    text-align: left;
                    width: 15%;
                    font-size: 14px;
                    line-height: 1.4;
                    padding-top: 5px;
                    padding-bottom: 5px;
                    &:nth-child(4){
                        width: 50%;
                    }
                    &:nth-child(1){
                        width: 20%;
                    }
                }
                th{
                    font-size: 18px;
                }
            }
        }
    }
</style>



